import React, {  useState } from "react";
import { Switch } from "platyplex_ui";
import Analytics from "./analytics";

enum Pages {
    "ANALYTICS" = "ANALYTICS",
    "LISTINGS" = "LISTINGS",
    "LIVE" = "LIVE"
};

const Events = () => {
    const [activePage, setActivePage] = useState<Pages>(Pages.ANALYTICS);
    const switches = <Switch.underlined
        style={{ placeSelf: "start", gridColumn: '1/-1' }}
        items={Object.keys(Pages)}
        onClick={(active: any) => setActivePage(active)}
        active={activePage}
    />
      
    return <Analytics switches={switches} /> 
      
};

export default Events;